import React from 'react';

import './experiences.scss';

import { getChildren } from './utils';

export function Experiences(props) {
    return <div className='experiences'>
        {props.children}
    </div>
}

export function Experience(props) {
    const children = getChildren(props.children);
    return <div className='experience'>
        <p className='timePeriod'>{children.StartDate} &ndash; {children.EndDate}</p>
        <div className='content'>
            <p className='b'>{children.Title}</p>
            <p><span className='b'>{children.Company}</span> &ndash; {children.Address}</p>
            <ul className='description'>
                {children.Description}
            </ul>
        </div>
    </div>
}

export function Company(props) {
    return props.children
}

export function Title(props) {
    return props.children
}

export function Description(props) {
    return props.children
}
